<template>
    <div id="analysisPage">
        <header-component />
        <div class="loading" v-loading="loading">
            <el-row >
                <el-col :span="2">
                    <div class="banner">
                        <div class="top-choose">
                            <div
                                :class="{ topic: true, highlight: !highlight_on }"
                                @click="handleToResult"
                            >
                                <img
                                    src="../assets/pictures/任务统计.svg"
                                    width="25px"
                                />
                                <div style="margin-left: 6px">统计结果</div>
                            </div>
                            <div
                                :class="{ topic: true, highlight: highlight_on }"
                                @click="handleToList"
                            >
                                <img
                                    src="../assets/pictures/列表.svg"
                                    width="22px"
                                />
                                <div style="margin-left: 6px">答卷列表</div>
                            </div>
                        </div>
                        <div>
                            <el-button type="new"
                                ><span class="span">返回</span></el-button
                            >
                        </div>
                    </div>
                </el-col>
                <el-col :span="22" :offset="2">
                    <router-view></router-view>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent.vue";
// import { mapState } from 'vuex'
export default {
    name: "HomePage",
    components: {
        HeaderComponent,
    },
    data() {
        return {
            tableData: [],
            dateRange: "",
            highlight_on: false,
            loading: false,
            onload: true
        };
    },
    watch: {
        $route(toRouter) {
            // list or sult
            let type = toRouter.path.slice(-4, toRouter.path.length);
            if (type == "list") {
                this.highlight_on = true;
            } else if (type == "sult") {
                this.highlight_on = false;
            }
        },
    },
    methods: {
        handleToResult() {
            this.$router.push("result");
        },
        handleToList() {
            this.$router.push("list");
        },
    },
    created() {
        let banner_type = this.$route.path;
        let type = banner_type.slice(-4, banner_type.length);
        if (type == "list") {
            this.highlight_on = true;
        } else if (type == "sult") {
            this.highlight_on = false;
        }
    },
};
</script>

